<template>
  <div class=" bg-dark">
    <!-- <loading :active.sync="isLoading"></loading> -->
    <CustomerNavbar/>
    <Alert></Alert>
    <div class="container">
      <div class="col-md-12">
        <div class="row">
          <div class="col-sm-12 col-md-12 text-center mb-2 sortList">
            <div class="sortBtn font-weight-bold" @click="goBack('')">
              
            </div>
          </div>
          <div class="col-3 col-md-3 text-center mb-2 sortList">
            <div class="sortBtn font-weight-bold" @click="goBack('categories')">
              
            </div>
          </div>
          <div class="col-3 col-md-3 text-center mb-2 sortList">
            <div class="sortBtn font-weight-bold" @click="goBack('categories')">
              
            </div>
          </div>
          <div class="col-3 col-md-3 text-center mb-2 sortList">
            <div class="sortBtn font-weight-bold" @click="goBack('categories')">
              
            </div>
          </div>
          <div class="col-3 col-md-3 text-center mb-2 sortList">
            <div class="sortBtn font-weight-bold" @click="goBack('categories')">
              
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="container d-flex justify-content-center align-items-center mb-3">
      <img src="https://upload.cc/i1/2018/11/04/Inh3gr.png" alt="" style="width: 60px;height: 60px;">
      <h1 class="d-inline-block mb-0 text-white pl-3"></h1>
    </div>
    <main class="container">
      <table class="table bg-white text-dark payTable">
        <thead>
          <th></th>
          <th>  </th>
          <th>  </th>
          <th>  </th>
        </thead>
        <tbody v-if="cart.carts">
          <tr v-for="item in cart.carts" :key="item.id" >
            <td class="align-middle">
              <button type="button" class="btn btn-outline-danger btn-sm"
              @click="removeCartItem(item.id)">
                <i class="far fa-trash-alt"></i>
              </button>
              <i class="fas fa-spinner fa-spin" v-if="cartRemove"></i>
            </td>
            <td class="align-middle">
              {{ item.product.title }}
              <div class="text-success" v-if="item.coupon">
                
              </div>
            </td>
            <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
            <td class="align-middle text-right">{{ item.final_total |currency}}</td>
          </tr>
        </tbody>
        <tfoot>
          <tr>
            <td colspan="3" class="text-right">  </td>
            <td class="text-right">{{ cart.total |currency}}</td>
          </tr>
          <tr v-if="cart.final_total !== cart.total">
            <td colspan="3" class="text-right text-success">  </td>
            <td class="text-right text-success">{{ cart.final_total |currency}}</td>
          </tr>
        </tfoot>
      </table>
      <!--  -->
      <div class="input-group mb-3 input-group-sm payCoupon">
        <input type="text" class="form-control" v-model="coupon_code"
        placeholder="  111 ?">
        <div class="input-group-append">
          <button class="btn btn-danger" type="button"
          @click="addCouponCode">
            
          </button>
        </div>
      </div>
      <div class="d-flex justify-content-center align-items-center mt-3">
        <img src="https://upload.cc/i1/2018/11/06/7XRo1P.png" alt="Logo" style="width: 60px;height: 60px;">
        <h2 class="d-inline-block mb-0 text-white h1 pl-4"></h2>
      </div>
      <!--  -->
      <div class="my-3 row justify-content-center">
        <form class="col-md-12" @submit.prevent="createOrder">
          <div class="form-group">
            <label class="text-lightTitle font-weight-bold" for="useremail">Email</label>
            <input type="email" class="form-control" name="email" id="useremail"
              v-validate="'required|email'"
              :class="{'is-invalid': errors.has('email')}"
              v-model="form.user.email" placeholder="  Email">
            <span class="text-danger font-weight-bold" v-if="errors.has('email')">
              {{ errors.first('email') }}
            </span>
          </div>
          <div class="form-group">
            <label class="text-lightTitle font-weight-bold" for="username">  </label>
            <input type="text" class="form-control" name="name" id="username"
              :class="{'is-invalid': errors.has('name')}"
              v-model="form.user.name" v-validate="'required'" placeholder="  ">
            <span class="text-danger font-weight-bold" v-if="errors.has('name')">  </span>
          </div>
          <div class="form-group">
            <label class="text-lightTitle font-weight-bold" for="usertel">  </label>
            <input type="tel" class="form-control" id="usertel" name="tel"
              v-model="form.user.tel" v-validate="'required'" placeholder="  ">
            <span class="text-danger font-weight-bold" v-if="errors.has('tel')">  </span>
          </div>
          <div class="form-group">
            <label class="text-lightTitle font-weight-bold" for="useraddress">  </label>
            <input type="address" class="form-control" name="address"
              :class="{'is-invalid': errors.has('address')}"
              id="useraddress" v-model="form.user.address" v-validate="'required'"
              placeholder="  ">
            <span class="text-danger font-weight-bold"
            v-if="errors.has('address')">  </span>
          </div>
          <div class="form-group">
            <label class="text-lightTitle font-weight-bold" for="usertextarea">  </label>
            <textarea name="" id="usertextarea" class="form-control" cols="30" rows="10"
              v-model="form.message"></textarea>
          </div>
          <div class="text-right">
            <button class="btn btn-warning font-weight-bold btn-block paySubmit">  </button>
          </div>
        </form>
      </div>
    </main>
    <hr class="stars">
    <footer class="container">
      <div class="row">
        <div class="col-md-4 mb-2">
          <img src="@/assets/navbar_img.png" alt="Logo">
        </div>
        <div class="col-md-4">
          <!-- <p class="text-white font-weight-bold h4"></p> -->
          <p class="text-white-gray font-weight-bold mb-0">00002400</p>
          <p class="text-white-gray font-weight-bold mb-0">09 - 12345678</p>
          <p class="text-white-gray font-weight-bold mb-0"> </p>
        </div>
        <div class="col-md-4 mb-3">
          <p class="text-white-gray font-weight-bold mb-0">EastM</p>
          <P class="text-white-gray font-weight-bold mb-0"></P>
          <P class="text-white-gray font-weight-bold mb-0"> https://www.google.com/</P>
          <p></p>
        </div>
      </div>
    </footer>
  </div>
</template>

<script>
import CustomerNavbar from '@/components/CustomerNavbar.vue';
import Alert from '@/components/AlertMessage.vue';

export default {
  components: {
    CustomerNavbar,
    Alert,
  },
  data() {
    return {
      // 
      status: {
        loadingItem: '',
      },
      // isLoading: false,
      cart: {},
      cart_lenght: 0,
      carStatu: false,
      coupon_code: '',
      cartRemove: false,
      form: {
        user: {
          name: '',
          email: '',
          tel: '',
          address: '',
        },
        message: '',
      },
    };
  },
  methods: {
    getCart() {
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`;
      const vm = this;
      vm.$store.dispatch('updateLoading', true);
      this.$http.get(api).then((response) => {
        console.log(response);
        vm.$store.dispatch('updateLoading', false);
        vm.cart = response.data.data;
        vm.cart_lenght = (vm.cart.carts).length;
      });
    },
    addCouponCode() {
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/coupon`;
      const vm = this;
      const coupon = {
        code: vm.coupon_code,
      };
      vm.$store.dispatch('updateLoading', true);
      this.$http.post(api, { data: coupon }).then((response) => {
        console.log(response);
        vm.$store.dispatch('updateLoading', false);
        vm.getCart();
      });
    },
    removeCartItem(id) {
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart/${id}`;
      const vm = this;
      vm.$store.dispatch('updateLoading', true);
      vm.cartRemove = true;
      this.$http.delete(api).then((response) => {
        console.log(response);
        vm.$store.dispatch('updateLoading', false);
        vm.cartRemove = false;
        vm.getCart();
      });
    },
    createOrder() {
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/order`;
      const vm = this;
      const order = vm.form;
      // vm.$store.dispatch('updateLoading', true);
      this.$validator.validate().then((result) => {
        if (result) {
          this.$http.post(api, { data: order }).then((response) => {
            console.log('', response);
            if (response.data.success) {
              vm.$router.push(`/pay_check/${response.data.orderId}`);
            }
            vm.$store.dispatch('updateLoading', false);
            // vm.getCart();
          });
        } else {
          console.log('');
        }
      });
    },
    goBack(page) {
      this.$router.push(`/${page}`);
    },
  },
  computed: {
    isLoading() {
      return this.$store.state.isLoading;
    },
  },
  created() {
    this.getCart();
  },
};
</script>
